const arr = [8, 87, 3, 0, -12, 88, 4351, 0.7];

function bs(arr, val){
    let guesIdx,
        minIdx = 0,
        maxIdx = arr.length -1;
    while(minIdx < maxIdx){
        guesIdx = Math.floor((minIdx+maxIdx)/2)
        if(arr[guesIdx]===val){
            return guesIdx
        }else if(arr[guesIdx]>arr[minIdx]){
            minIdx = guesIdx+1
        }else{
            maxIdx = guesIdx - 1
        }
    }
    return -1
}


import { test } from 'ava'

test('index of 0 is 3', t => t.is(bs(arr, 0), 3))